elime bohatú fotosyntézu a prosperitu rastlinám. Zemný substrát musí dýchať. Vzdušnosť koreňového systému napomáha rastu a vitalite. Či už manuálna alebo automatická. Správne načasovaná závlaha, ako aj množstvo vody sú pre život vegetačných stien rovnako dôležité ako svetlo a pôda. ")]),_c('div',{staticClass:"image",style:({ backgroundImage : 'url('+_vm.image2+')' })}),_vm._m(4),_c('p',[_vm._v(" je dnes moderný a veľmi populárny systém pestovania rastlín. Prináša so sebou odľahčenie vegetačných interiérových stien, a tak umožňuje ich realizáciu aj na miestach „s krehkejšou statikou“. Vďaka odľahčeniu je možné ich realizovať v každom interiéri a do veľkých výšok. Pre hydroponickú výsadbu sú vhodné vybrané druhy rastlín. Ich rôznorodosť a farebnosť umožňuje vytvárať zaujímavé a estetické zoskupenia. ")]),_c('p',[_vm._v(" je dnes moderný a veľmi populárny systém pestovania rastlín. Prináša so sebou odľahčenie vegetačných interiérových stien, a tak umožňuje ich realizáciu aj na miestach „s krehkejšou statikou“. Vďaka odľahčeniu je možné ich realizovať v každom interiéri a do veľkých výšok. Pre hydroponickú výsadbu sú vhodné vybrané druhy rastlín. Ich rôznorodosť a farebnosť umožňuje vytvárať zaujímavé a estetické zoskupenia. ")]),_c('p',[_vm._v(" Svetlo, voda a hnojivá sú pre hydroponické vegetačné steny kľúčové. Podobne, ako pri pestovaní v substráte, je pre rastliny mimoriadne dôležité správne svetlo a správne načasovanie závlahy, ako aj množstvo vody, ktoré procesom závlahy prejde. V neposlednom rade pre správny rast a vitalitu je potrebné rastlinky hnojiť. Overeným riešením sú rozpustné hnojivá do vody. Práve chýbajúci substrát, ktorý v sebe skrýva rad užitočných látok pre rast a vývoj rastlín, je pri hydroponickom pestovaní nahradený správnymi hnojivami. ")])]),_vm._m(5)]),_vm._m(6)],1)}
