<script>
    import Divider from "../components/Divider";
    import Header from "../components/Header";
    import emailjs from 'emailjs-com';
    import { HalfCircleSpinner } from 'epic-spinners'
    export default {
        name: 'App',
        components: {
            Divider,
            Header,
            HalfCircleSpinner
        },
        data(){
            return {
                email:{
                    text: '',
                    phone: '',
                    email: '',
                },
                textErr: '',
                phoneErr: '',
                emailErr: '',
                reg: RegExp(
                    /^[a-zA-Z0-9.!#$%&’*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/
                ),
                regPhone: RegExp(
                    /\+421\d{9}/
                ),
                emailSuccess: false,
                emailFailed: false,
                loading: false,
            }
        },
        methods:{
            sendEmail(e){
                var errors = 0;
                if(!this.email.text){
                    this.textErr='Vyplňte pole so správou!';
                    errors++;
                }else {
                    this.textErr='';
                }
                if(!this.email.email){
                    this.emailErr='Vyplňte pole s e-mailom!';
                    errors++;
                }else{
                    this.emailErr='';
                    if(!this.reg.test(this.email.email)){
                        errors++;
                        this.emailErr = "Zlý formát e-mailu";
                    }else {
                        this.emailErr='';
                    }
                }
                if(!this.email.phone){
                    this.phoneErr = 'Vyplňte pole s telefónnym číslom!';
                    errors++;
                }else{
                    this.phoneErr = '';
                    if(!this.regPhone.test(this.email.phone)){
                        errors++;
                        this.phoneErr = "Zlý formát telefónneho čísla! Skúste formát +421908908908"
                    }else{
                        this.phoneErr = ''
                    }
                }
                if(errors == 0){
                    this.loading = true;
                    this.sendTemplateEmail(e)
                }
            },
            async sendTemplateEmail(e){
                try {
                    emailjs.sendForm('service_rg063vo', 'template_t7g8wse', e.target, 'user_tc3CBjPuSyTwP4i76iVUi')
                        .then((result) => {
                            this.loading = false;
                            this.emailSuccess = true;
                            console.log('SUCCESS!', result.status, result.text);
                        }, (error) => {
                            this.loading = false
                            this.emailFailed = true;
                            console.log('FAILED...', error);
                        });

                } catch(error) {
                    console.log({error})
                }
            }

        },
        created(){
            document.title = 'Kontakt | k2flora'
        }
    }
</script>

