        width: 100px;
    }
    .empty-article {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 60vh;
        font-size: 32px;
        color: #a6a6a6;
    }
    @media screen and (max-width: 980px) {
        .wrapper{
            width: auto;
        }
