var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',[_c('Header',{attrs:{"text":"Blog","image":require('@/assets/gallery/machove-steny/01.jpg')}}),_c('div',{staticClass:"wrapper"},[_vm._m(0),_vm._m(1),_c('div',{staticClass:"content"},[_vm._m(2),_c('p',[_vm._v(" V letných dňoch, kedy sa teplota dostane na 30 °C, je teplota bežne ploche strechy na povrchu okolo 70 °C. Teplota v rovnakom čase na vegetačnej streche sa pohybuje okolo 27 °C-30 °C. ")]),_c('p',[_vm._v(" Ako je to možné? Vegetačné strechy sú “zásobárne vody”. Vďaka tomu, v lete odparujúca sa voda chladí okolitý priestor. Odparením jedného litra vody pritom budova ušetrí približne 0,7 kWh energie potrebnej na prevádzku chladenia. Ak si to premietneme na plochu a vyčíslime úsporu, tak 1 mm zrážok zachytených v retenčnom systéme vegetačnej strechy, na ploche 100m2 dokáže svojím odparením ušetriť 70 kWh energie potrebnej na chladenie vnútorných priestorov budovy v letných dňoch! ")]),_vm._m(3),_c('p',[_vm._v(" Či už extenzívna, polo intenzívna alebo intenzívna, o všetky sa treba primerane starať. Údržba extenzívnej strechy je nenáročná. Spočíva v občasnom odburinení, dosadení vypadnutých rastlín, či v letných mesiacoch pri dlhšie trvajúcom suchu, v miernej závlahe. S výškou substrátu rastie aj náročnosť starostlivosti. Avšak ani pri intenzívnych strechách sa nedá hovoriť o údržbe, ktorú by človek sám nezvládol. ")]),_vm._m(4),_c('p',[_vm._v(" Podľa typu strechy a intenzity výsadby dokáže 10 m2 extenzívnej zelenej strechy vyprodukovať za 1 deň kyslík pre 1 dospelého človeka! Pri vegetačných fasádach dokáže rovnaká plocha vegetácie vyprodukovať kyslík až pre 30-tich dospelých ľudí za deň! Priamo úmerne k produkcii kyslíka patrí aj spotreba oxidu uhličitého. ")]),_c('div',{staticClass:"image",style:({ backgroundImage : 'url('+_vm.image+')' })}),_vm._m(5),_c('p',[_vm._v(" O niečo vyššie vstupné náklady na realizáciu sú kompenzované s celým radom pozitívnych aspektov vegetačných striech opísaných vyššie. Vstupné investície dnes už nie sú merateľným nástrojom pre kvantitatívne zaradenia budovy do oblasti drahá/lacná. Merateľnými faktormi sú prevádzkové náklady, tvorba emisií, čas za ktorý je potrebná opätovná obnova stavby či jej častí no a s tým spojená uhlíková stopa. ")]),_vm._m(6),_c('p',[_vm._v(" Vegetačná strecha plní funkciu izolácie. V zime prispieva k udržaniu tepla v budove a v lete naopak, chráni vnútorný priestor pred prehrievaním. V kombinácii s odborne vypracovanými detailmi zvyšku stavby tak dokáže významne eliminovať potrebu budovy na vykurovanie a chladenie. ")]),_vm._m(7),_c('p',[_vm._v(" Vegetačné strechy sú priateľom alergikov. Sú to prírodné práčky vzduchu. Vďaka odparovaniu vody zvyšujú vlhkosť vzduchu, čo pomáha k lepšiemu pocitu z dýchania v ich blízkosti. Absorbujú prach z okolitého prostredia. Rastliny pohlcujú CO2 a produkujú kyslík. Vegetácia na streche tvorí prírodnú protihlukovú stenu. Tlmí hluk z prostredia a zlepšuje kvalitu bývania. ")])])]),_vm._m(8)],1)}
