var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',[_c('Header',{attrs:{"text":"Blog","image":require('@/assets/gallery/machove-steny/01.jpg')}}),_c('div',{staticClass:"wrapper"},[_vm._m(0),_vm._m(1),_c('div',{staticClass:"content"},[_c('p',[_vm._v(" S čoraz viac otepľujúcou sa klímou a zmenami, ktoré spôsobilo ľudstvo samo, prichádzajú do našich zemepisných šírok teplotné a zrážkové výkyvy. S meniacou sa klímou je potrebné sa adaptovať a zmeniť aj naše návyky. Správne zmeny v našom myslení totižto dokážu spomaliť, ak nie rovno zastaviť, hrozivé scenáre klimatických zmien. ")]),_c('p',[_vm._v(" Práve obnovenie životného prostredia, vyčistenie ovzdušia a zvýšenie produkcie kyslíka, čo priamoúmerne znižuje hladinu oxidu uhličitého v atmosfére, sú kľúčové faktory, ktorým sa budeme venovať na príklade a realizácii vegetačných striech. V K2flore sme stavili na profesionalitu, odbornosť a maximálne úsilie priblížiť vegetačné strechy širokému spektru ľudí, a tým prispieť svojou troškou k ich aktívnej implementácii. Pretože s nimi je život a naše prostredie oveľa zdravšie. ")]),_c('div',{staticClass:"image",style:({ backgroundImage : 'url('+_vm.image1+')' })}),_c('p',[_vm._v(" Vegetačné strechy sú spomínané už v staroveku a ich história plynule pokračuje cez antiku až po novovek. Nie sú žiadnym novotvarom ani fenoménom dnešnej doby. Len sa na ne akoby medzičasom pozabudlo a ich krásu a funkčnosť znovu objavujeme. ")]),_c('p',[_vm._v(" O dôležitosti vegetačných striech hovoria samotné dejiny. Visuté záhrady kráľovnej Semiramis (z približne 8. storočia p. n. l.) patria medzi jeden zo siedmich divov sveta napriek tomu, že sa do dnešnej doby nezachovali. ")]),_c('p',[_vm._v(" Zelené strechy sú atraktívnym a výrazným architektonickým prvkom stavieb. Do prostredia, v ktorom sa nachádzajú, prinášajú široké spektrum pozitívnych aspektov ako napríklad: ")]),_vm._m(2),_c('p',[_vm._v(" Na naše územie sa vegetačné strechy opäť prina