rých hodnoty môžu dosahovať aj viac ako 300 % úspory. ")]),_c('p',[_vm._v(" Intenzívne zelené strechy sú atraktívnym priestorom. Svoje miesto si u nás našli na každom zaujímavom projekte biznis centra, či nákupného centra. Ich potenciál bol objavený. Stali sa pútačom a lákadlom pre zákazníkov, ktorých lákajú na posedenie či kávičku. ")]),_c('div',{staticClass:"image",style:({ backgroundImage : 'url('+_vm.image3+')' })})]),_vm._m(7),_vm._m(8),_vm._m(9)]),_vm._m(10)],1)}
